.footer {
	.top-block {
		padding-bottom: 62px;
		border-bottom: 1px solid rgba(#fff, 0.2);
		display: flex;
		justify-content: space-between;

		@media (max-width: $mobile) {
			padding-bottom: 40px;
			display: block;
		}

		.left-block {
			display: flex;
			align-items: center;

			@media (max-width: $tablet) {
				flex-direction: column;
				align-items: flex-start;
			}

			@media (max-width: $mobile) {
				display: block;
				text-align: center;
			}

			.logo-block {
				display: flex;
				align-items: center;
				margin-right: 47px;

				@media (max-width: $desk) {
					margin-right: 10px;
				}

				@media (max-width: $mobile) {
					display: block;
				}

				.logo {
					display: inline-flex;
					flex: 1 0 238px;
					width: 210px;
					height: 66px;
					background: url(../img/big-logo.svg) 0 0 no-repeat;

					@media (max-width: $desk) {
						background-size: 100% auto;
						width: 150px;
						flex: 1 0 150px;
						height: 48px;;
					}

					@media (max-width: $mobile) {
						background: url(../img/logo.svg) 0 0 no-repeat;
						width: 92px;
						height: 87px;
						background-size: 100% auto;;
						display: flex;
						margin: 0 auto 12px;
					}
				}

				.info-logo {
					position: relative;
					flex: 0 230px;
					width: 230px;
					display: inline-flex;
					font-size: 14px;
					color: #fff;
					font-weight: 500;
					margin-left: 26px;
					padding-left: 25px;

					@media (max-width: $desk) {
						margin-left: 15px;
						padding-left: 15px;
					}

					@media (max-width: $mobile) {
						padding: 0 20px;
						margin: 0;
					}

					&:before {
						position: absolute;
						top: 50%;
						left: 0;
						content: '';
						border-left: 1px solid #fff;
						opacity: 0.2;
						height: 54px;
						margin-top: -27px;

						@media (max-width: $mobile) {
							display: none;
						}
					}
				}
			}

			.soc-block {
				display: flex;
				align-items: center;
				margin: 0 -7px;

				@media (max-width: $tablet) {
					margin-top: 30px;
				}

				@media (max-width: $mobile) {
					justify-content: center;
				}

				.soc-link {
					position: relative;
					flex: 1 0 28px;
					max-width: 28px;
					width: 28px;
					height: 28px;
					margin: 0 7px;
					transition: all .3s ease-in-out;
					@include delay(animation, 10, 0.25s);

					&:hover {
						&:before {
							background-color: #827AFF !important;
						}
					}

					&:before {
						position: absolute;
						top: 50%;
						left: 50%;
						content: '';
						transform: translate(-50%, -50%);
						transition: all .3s ease-in-out;
					}

					&.inst {
						&:before {
							-webkit-mask: url(../img/inst-icon.svg) no-repeat 50% 50%;
					    mask: url(../img/inst-icon.svg) no-repeat 50% 50%;
					    -webkit-mask-size: 100% auto;
					    mask-size: 100% auto;
					    background-color: #eee;
					    width: 20px;
					    height: 20px;
						}
					}

					&.fb {
						&:before {
							-webkit-mask: url(../img/fb-icon.svg) no-repeat 50% 50%;
					    mask: url(../img/fb-icon.svg) no-repeat 50% 50%;
					    -webkit-mask-size: 100% auto;
					    mask-size: 100% auto;
					    background-color: #eee;
					    width: 14px;
					    height: 23px;
						}
					}

					&.yt {
						&:before {
							-webkit-mask: url(../img/yt-icon.svg) no-repeat 50% 50%;
					    mask: url(../img/yt-icon.svg) no-repeat 50% 50%;
					    -webkit-mask-size: 100% auto;
					    mask-size: 100% auto;
					    background-color: #eee;
					    width: 24px;
					    height: 17px;
						}
					}
				}
			}
		}

		.right-block {
			display: flex;
			align-items: center;

			@media (max-width: $desk) {
				display: block;
			}

			@media (max-width: $mobile) {
				margin-top: 26px;
				padding-top: 26px;
				border-top: 1px solid #fff2;
				text-align: center;
			}

			.question-block {
				flex: 0 auto;
				margin-right: 28px;

				@media (max-width: $desk) {
					margin: 0 0 30px;
				}

				.title {
					display: block;
					font-size: 22px;
					font-weight: bold;
					color: #fff;
					margin-bottom: 5px;
					line-height: 1.1em;

					@media (max-width: $mobile) {
						font-size: 16px;
					}
				}

				.info {
					display: block;
					font-size: 18px;
					font-weight: 500;
					color: rgba(#fff, 0.7);
					line-height: 1.1em;

					@media (max-width: $mobile) {
						font-size: 14px;
					}
				}
			}

			.f_button {
				width: 230px;

				@media (max-width: $mobile) {
					width: 176px;
					padding: 0;
					height: 40px;
				}
			}
		}
	}

	.bottom-block {
		padding: 37px 0;
		display: flex;
		align-items: center;
		justify-content: space-between;

		@media (max-width: $mobile) {
			flex-direction: column;
			padding: 20px 0 45px;
		}

		.copyright {
			display: inline-flex;
			font-size: 16px;
			font-weight: 500;
			color: #eee;
			line-height: 1.1em;

			@media (max-width: $mobile) {
				font-size: 14px;
				order: 2;
			}
		}

		.right-block {
			display: flex;
			align-items: center;

			@media (max-width: $mobile) {
				width: 100%;
				order: 1;
				justify-content: space-between;
				margin-bottom: 40px;
			}

			.title {
				display: flex;
				flex-wrap: wrap;
				font-size: 16px;
				font-weight: bold;
				color: #fff;
				line-height: 1.1em;
				width: 190px;
				flex: 1 0 190px;

				@media (max-width: $mobile) {
					font-size: 14px;
				}

				i {
					display: block;
					font-style: normal;
					font-weight: 300;
					width: 100%;
				}
			}

			.thanks-logo {
				display: inline-flex;
				background: url(../img/garant-icon.svg) 0 0 no-repeat;
				width: 74px;
				height: 42px;

				&:hover {
					opacity: .3;
				}
			}
		}
	}
}