.modal {
  position: fixed;
  z-index: 10000; /* 1 */
  top: 0;
  left: 0;
  right: 0;
  visibility: hidden;
  width: 100%;
  height: calc(100% - 40px);
  // transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  
  
}

.modal.is-visible {
  visibility: visible;

  .modal-wrapper {
    // animation: bounce-in-top 1.1s both;
    animation: fade-in-fwd 1s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
  }
  
}

@-webkit-keyframes puff-in-center {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-filter: blur(4px);
            filter: blur(4px);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-filter: blur(0px);
            filter: blur(0px);
    opacity: 1;
  }
}
@keyframes puff-in-center {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-filter: blur(4px);
            filter: blur(4px);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-filter: blur(0px);
            filter: blur(0px);
    opacity: 1;
  }
}

@-webkit-keyframes bounce-in-top {
  0% {
    -webkit-transform: translateY(-500px);
            transform: translateY(-500px);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    opacity: 0;
  }
  38% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    opacity: 1;
  }
  55% {
    -webkit-transform: translateY(-65px);
            transform: translateY(-65px);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  72% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
  81% {
    -webkit-transform: translateY(-28px);
            transform: translateY(-28px);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  90% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
  95% {
    -webkit-transform: translateY(-8px);
            transform: translateY(-8px);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
}
@keyframes bounce-in-top {
  0% {
    -webkit-transform: translateY(-500px);
            transform: translateY(-500px);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    opacity: 0;
  }
  38% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    opacity: 1;
  }
  55% {
    -webkit-transform: translateY(-65px);
            transform: translateY(-65px);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  72% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
  81% {
    -webkit-transform: translateY(-28px);
            transform: translateY(-28px);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  90% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
  95% {
    -webkit-transform: translateY(-8px);
            transform: translateY(-8px);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
}

@-webkit-keyframes fade-in-fwd {
  0% {
    -webkit-transform: translateZ(-80px);
            transform: translateZ(-80px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    opacity: 1;
  }
}
@keyframes fade-in-fwd {
  0% {
    -webkit-transform: translateZ(-80px);
            transform: translateZ(-80px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    opacity: 1;
  }
}


.open-modal {
  overflow: hidden;
}

.modal.is-visible {
  visibility: visible;
}

.modal-overlay {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(#282b30, 0.8);
  visibility: hidden;
  opacity: 0;
  cursor: pointer;
  transition: all .3s;
}

.modal.is-visible .modal-overlay {
  opacity: 1;
  visibility: visible;
  transition-delay: 0s;
}

.modal-wrapper {
  position: relative;
  z-index: 9999;
  width: calc(100% - 30px);
  background-color: #fff;
  padding: 40px 45px;

  @media (max-width: $mobile) {
    width: 100%;
    padding: 40px 15px 30px;
  }
}

.modal-transition {
  transition: all 0.3s;
  opacity: 0;
}

.modal.is-visible .modal-transition {
  opacity: 1;
}

.modal-header,
.modal-content {
  padding: 1em;
}

.modal-header {
  padding: 0;

}

.modal-close {
  position: absolute;
  top: 0;
  right: 23px;
  border: 0;
  width: 58px;
  height: 58px;
  background: rgba(#1F2126, 0.9);
  transition: all .3s;
  padding: 0;
  z-index: 99991;

  &:before {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-mask: url(../img/close.svg) no-repeat 50% 50%;
    mask: url(../img/close.svg) no-repeat 50% 50%;
    -webkit-mask-size: cover;
    mask-size: cover;
    background-color: #fff;
    width: 14px;
    height: 14px;
    transition: all .3s;
    content: '';
    margin-top: -7px;
    margin-left: -7px;
  }
  

  @media (max-width: $mobile) {
    top: 0px;
    right: 8px;
    background-color: #1f1d34;
  }

  &:hover {
    background-color: #fff;
    &:before {
      background-color: #0009;
    }
  }
}
.modal-content {
  padding: 0;
  max-width: 540px;
  margin: 0 auto;
}

.modal-heading {
  display: flex;
  font-size: 22px;
  font-weight: bold;
  color: #fff;
  justify-content: center;
  margin-bottom: 10px;
}

.head-description {
  display: flex;
  font-weight: 400;
  color: #fff;
  justify-content: center;
  margin-bottom: 20px;
  text-align: center;

  @media (max-width: $mobile) {
    font-size: 14px;
  }
}

#modalService {
  margin: 22px auto;
  max-width: calc(1450px + 44px);

  @media (max-width: $mobile) {
    width: calc(100% - 44px);
  }

  .modal-wrapper {
    background: linear-gradient(0deg, #1F2126, #1F2126), #F8F8F8;
    overflow: hidden;
    overflow-y: auto;
    height: 100%;
    padding: 0;

    &::-webkit-scrollbar-track {
      background-color: #fff;
    }

    &::-webkit-scrollbar {
      width: 8px;
      background-color: #fff;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #827AFF;
    }
  }
  .modal-content {
    max-width: 100%;
  }

  .video {
    position: relative;
    width: 100%;
    height: 566px;
    // background: url(../img/service-image-1.jpg) 50% 50% no-repeat;
    // background-size: cover;

    @media (max-width: $mobile) {
      height: 243px;
    }

    &:first-child {
      margin-top: 0;
    }

    &:before {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      content: '';
      background: linear-gradient(180deg, rgba(31, 33, 38, 0) 0%, #1F2126 100%);
    }

  }

  .description-block {
    position: relative;
    text-align: center;
    margin-top: -80px;
    padding: 0 90px;

    @media (max-width: $tablet) {
      padding: 0 22px;
    }

    @media (max-width: $mobile) {
      margin-top: -10px;
      text-align: left;
    }

    .main-title {
      @media (max-width: $mobile) {
        font-size: 22px;
      }
    }

    .button-group {
      display: flex;
      justify-content: center;
      margin: 26px -4px 0;

      @media (max-width: $mobile) {
        display: block;
        margin: 23px 0 0;
      }

      .f_button {
        margin: 0 4px;
        height: 57px;

        @media (max-width: $mobile) {
          margin: 8px 0;
          height: 50px;
          padding: 0;
          width: 100%;
        }

        &.border-button {
          border: 1px solid #7069EE;
          background: transparent;

          &:before {
            display: inline-flex;
            content: '';
            -webkit-mask: url(../img/pdf-icon.svg) no-repeat 50% 50%;
            mask: url(../img/pdf-icon.svg) no-repeat 50% 50%;
            -webkit-mask-size: cover;
            mask-size: cover;
            background-color: #fff;
            transition: all .3s;
            width: 18px;
            height: 22px;
            margin-right: 12px;
            z-index: 1;
          }
        }
      }
    }
  }

  .why-we-section {
    padding: 70px 92px 100px;

    @media (max-width: $desk) {
      padding: 70px 22px 100px;
    }

    @media (max-width: $mobile) {
      padding: 30px 22px 45px;
    }

    .container {
      padding: 0;
    }

    .top-block {
      padding: 0;
      .wrapper {
        .left-block {
          .inner-wrapper {
            .item:first-child {
              @media (max-width: $mobile) {
                padding-right: 100px;
              }
            }
          }
        }
        .right-block {
          ul {
            column-count: 1;

            li {
              margin: 40px 0 0;

              @media (max-width: $mobile) {
                margin-top: 20px;
              }

              &:first-child {
                margin-top: 0;
              }
            }
          }
        }
      }
    }
  }

  .portfolio-section {
    padding: 0 92px 75px;

    @media (max-width: $tablet) {
      padding: 0 22px 50px;
    }

    .swiper-button-prev {
      left: 0;

      @media (max-width: $mobile) {
        left: 50%;
        bottom: 220px;
        width: 58px;
        margin-left: -60px;
      }

      &:after {
        @media (max-width: $mobile) {
          width: 58px;
        }
      }
    }
    .swiper-button-next {
      right: 0;

      @media (max-width: $mobile) {
        right: 50%;
        bottom: 219px;
        width: 58px;
        margin-right: -60px;
      }

      &:after {
        @media (max-width: $mobile) {
          width: 58px;
        }
      }
    }
    
    .slider {
      margin: 0 -92px;

      @media (max-width: $mobile) {
        margin: 0;
      }
    }

    .button-group {
      display: flex;
      justify-content: center;
      margin: 65px -4px 0;

      @media (max-width: $mobile) {
        display: block;
        margin: 0;
        padding-top: 93px;
      }

      .f_button {
        margin: 0 4px;
        height: 57px;

        @media (max-width: $mobile) {
          margin: 8px 0;
          height: 50px;
          padding: 0;
          width: 100%;
        }

        &.border-button {
          border: 1px solid #7069EE;
          background: transparent;

          &:before {
            display: inline-flex;
            content: '';
            -webkit-mask: url(../img/pdf-icon.svg) no-repeat 50% 50%;
            mask: url(../img/pdf-icon.svg) no-repeat 50% 50%;
            -webkit-mask-size: cover;
            mask-size: cover;
            background-color: #fff;
            transition: all .3s;
            width: 18px;
            height: 22px;
            margin-right: 12px;
            z-index: 1;
          }
        }
      }
    }
  }
}

#modalPolicy,
#modalForm {
  .modal-wrapper {
    background: linear-gradient(0deg, #1F2126, #1F2126), #F8F8F8;
    box-shadow: 20px 20px 60px rgba(62, 63, 67, 0.1), 0px 0px 50px rgba(0, 0, 0, 0.15);
  }
}

.modal-feedback {
  max-width: 480px;
  padding: 70px 48px 55px;

  @media (max-width: $mobile) {
    padding: 50px 10px 10px;
  }

  .modal-close {
    right: 0;
  }

  .f_group {
    margin-top: 14px;

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-top: 30px;
      display: flex;
      justify-content: center;

      @media (max-width: $mobile) {
        margin-top: 14px;
      }
    }
  }
  .f_button {
    height: 57px;
    max-width: 230px;
  }
}

.modal-review {
  max-width: 480px;
  padding: 70px 48px 55px;


  .modal-close {
    right: 0;
  }

  .modal-content {
    position: relative;
    overflow: hidden;
  }

  .scrolling-block {
    position: relative;
    overflow: hidden;
    height: 325px;
    overflow-y: auto;
    padding-right: 10px;

    @media (max-width: $mobile) {
      height: 300px;
    }

    &::-webkit-scrollbar-track {
      background-color: rgba(#fff, 0.1);
      border-radius: 5px;
    }

    &::-webkit-scrollbar {
      width: 4px;
      border-radius: 5px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #7069EE;
      border-radius: 5px;
    }    

    p {
      position: relative;
      font-weight: 400;
    }
  }
}