.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	height: 195px;
	transition: all .3s;
	z-index: 11;

	@media (max-width: $tablet) {
		height: 155px;
	}

	@media (max-width: $mobile) {
		height: 100px;
	}

	&:before {
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		content: '';
		background: linear-gradient(180deg, rgba(31, 33, 38, 0) 0%, #1F2126 100%);
		transform: matrix(1, 0, 0, -1, 0, 0);
		z-index: -1;
		transition: all .3s;
	}

	&.fixed {
		height: 100px;

		@media (max-width: $mobile) {
			height: 80px;
		}

		.wrapper {
			.logo {
				width: 90px;
				height: 84px;

				@media (max-width: $mobile) {
					width: 110px;
					height: 40px;
				}
			}
		}
	}

	.container {
		position: relative;
		height: 100%;
		z-index: 11;
	}

	.wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 100%;

		.logo {
			display: inline-flex;
			width: 128px;
			height: 120px;
			background: url(../img/logo.svg) 0 0 no-repeat;
			background-size: 100% auto;

			@media (max-width: $mobile) {
				background: url(../img/big-logo.svg) 0 0 no-repeat;
				width: 133px;
				height: 42px;
				background-size: 100% auto;
			}
		}

		.right-block {
			display: flex;
			align-items: center;
			height: 100%;

			.phone-link {
				position: relative;
				display: inline-flex;
				font-size: 22px;
				padding-left: 36px;

				@media (max-width: $mobile) {
					display: none;
				}

				&:hover {
					color: #eee5;

					&:before {
						background-color: #eee5;
					}
				}

				&:before {
					position: absolute;
					top: 50%;
					left: 0;
					content: '';
					-webkit-mask: url(../img/phone-icon.svg) no-repeat 50% 50%;
		      mask: url(../img/phone-icon.svg) no-repeat 50% 50%;
		      -webkit-mask-size: 100% auto;
		      mask-size: 100% auto;
		      background-color: #eee;
		      -webkit-transition: all .2s ease-in-out;
		      transition: all .2s ease-in-out;
		      width: 26px;
		      height: 26px;
		      margin-top: -13px;
				}
			}

			.lang-switcher {
				display: flex;
				align-items: center;
				margin-left: 135px;

				@media (max-width: $tablet) {
					margin-left: 50px;
				}

				@media (max-width: $mobile) {
					display: none;
				}

				.lang-link {
					display: inline-flex;
					font-size: 22px;
					font-weight: 700;
					line-height: 1em;
					text-transform: uppercase;
					color: rgba(#EEEEEE, 0.3);
					margin: 0 7.5px;

					&.active {
						color: #eee;
					}

					&:hover {
						color: #eee4;
					}
				}
			}

			.hamburger {
				margin-left: 50px;
			}
		}
	}

	.header-menu {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: #1F2126;
		z-index: 10;
		opacity: 0;
		visibility: hidden;
		overflow: hidden;
		overflow-y: auto;
		padding: 280px 0 65px;
		transition: all .5s;
		animation: puff-out-bl 0.9s cubic-bezier(0.165, 0.840, 0.440, 1.000) both;


		@media (max-width: $mobile) {
			padding: 100px 0 0;
		}


		.container {
			// height: auto;
			display: flex;
			flex-direction: column;
			justify-content: center;
		}

		.header-navbar-menu {
			display: flex;
			flex-direction: column;
			@include reset-list;

			& > li {
				margin-top: 22px;

				@media (max-width: $mobile) {
					margin-top: 34px;
				}

				&:first-child {
					margin-top: 0;
				}

				& > a {
					position: relative;
					display: inline-flex;
					font-size: 58px;
					font-weight: 700;
					line-height: 1.1em;
					color: #eee;

					@media (max-width: $mobile) {
						font-size: 18px;
					}

					&:hover {
						color: #7069EE;
					}
				}

				&:nth-child(4),
				&:nth-child(5) {
					margin-top: 47px;

					@media (max-width: $mobile) {
						margin-top: 34px;
					}

					& > a {
						font-size: 22px;
						font-weight: 400;

						@media (max-width: $mobile) {
							font-size: 18px;
							font-weight: 700;
						}
					}
				}

				&:nth-child(5) {
					margin-top: 32px;

					@media (max-width: $mobile) {
						margin-top: 34px;
					}
				}

				&.menu-item-has-children {
					& > a {
						padding-right: 41px;

						@media (max-width: $mobile) {
							padding-right: 20px;
						}

						&:hover {
							&:before {
								background-color: #7069EE;
							}
						}

						&:before {
							position: absolute;
							top: 50%;
							right: 0;
							content: '';
							// background: url(../img/triangle-bottom.svg) 0 0 no-repeat;
							-webkit-mask: url(../img/triangle-bottom.svg) no-repeat 50% 50%;
				      mask: url(../img/triangle-bottom.svg) no-repeat 50% 50%;
				      -webkit-mask-size: 100% auto;
				      mask-size: 100% auto;
				      background-color: #fff;
				      transition: all .3s;
							width: 25px;
							height: 14px;

							@media (max-width: $mobile) {
								width: 9px;
								height: 5px;
								margin-top: -2.5px;
							}
						}
					}

					&.open-sub {
						& > a {
							&:before {
								transform: rotate(180deg);
							}
						}
					}
				}
			}
		}

		.sub-menu {
			display: none;
			width: 500px;
			@include reset-list;
			@include clearfix;
			margin-top: 25px;

			@media (max-width: $mobile) {
				width: 100%;
			}

			li {
				width: 50%;
				float: left;
				margin-bottom: 14px;

				@media (max-width: $mobile) {
					width: 100%;
					margin-bottom: 8px;
				}

				a {
					display: inline-flex;
					font-size: 22px;
					font-weight: 400;
					color: #eee;
					line-height: 1.1em;

					@media (max-width: $mobile) {
						font-size: 14px;
					}

					&:hover {
						color: #7069EE;
					}
				}
			}
		}

		.soc-block {
			display: flex;
			align-items: center;
			margin: 140px -7px 0;

			@media (max-width: $mobile) {
				margin-top: 70px;
			}

			.soc-link {
				position: relative;
				flex: 1 0 28px;
				max-width: 28px;
				width: 28px;
				height: 28px;
				margin: 0 7px;
				transition: all .3s ease-in-out;

				&:hover {
					&:before {
						background-color: #827AFF !important;
					}
				}

				&:before {
					position: absolute;
					top: 50%;
					left: 50%;
					content: '';
					transform: translate(-50%, -50%);
					transition: all .3s ease-in-out;
				}

				&.inst {
					&:before {
						-webkit-mask: url(../img/inst-icon.svg) no-repeat 50% 50%;
				    mask: url(../img/inst-icon.svg) no-repeat 50% 50%;
				    -webkit-mask-size: 100% auto;
				    mask-size: 100% auto;
				    background-color: #eee;
				    width: 20px;
				    height: 20px;
					}
				}

				&.fb {
					&:before {
						-webkit-mask: url(../img/fb-icon.svg) no-repeat 50% 50%;
				    mask: url(../img/fb-icon.svg) no-repeat 50% 50%;
				    -webkit-mask-size: 100% auto;
				    mask-size: 100% auto;
				    background-color: #eee;
				    width: 14px;
				    height: 23px;
					}
				}

				&.yt {
					&:before {
						-webkit-mask: url(../img/yt-icon.svg) no-repeat 50% 50%;
				    mask: url(../img/yt-icon.svg) no-repeat 50% 50%;
				    -webkit-mask-size: 100% auto;
				    mask-size: 100% auto;
				    background-color: #eee;
				    width: 24px;
				    height: 17px;
					}
				}
			}
		}
	}
}

.open-menu {
	overflow: hidden;
	.header-menu {
		opacity: 1;
		visibility: visible;
		animation: puff-in-bl 0.7s cubic-bezier(0.470, 0.000, 0.745, 0.715) both;
	}
}

@-webkit-keyframes puff-in-bl {
  0% {
    -webkit-transform: scale(2);
            transform: scale(2);
    -webkit-transform-origin: 0% 100%;
            transform-origin: 0% 100%;
    -webkit-filter: blur(4px);
            filter: blur(4px);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform-origin: 0% 100%;
            transform-origin: 0% 100%;
    -webkit-filter: blur(0px);
            filter: blur(0px);
    opacity: 1;
  }
}
@keyframes puff-in-bl {
  0% {
    -webkit-transform: scale(2);
            transform: scale(2);
    -webkit-transform-origin: 0% 100%;
            transform-origin: 0% 100%;
    -webkit-filter: blur(4px);
            filter: blur(4px);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform-origin: 0% 100%;
            transform-origin: 0% 100%;
    -webkit-filter: blur(0px);
            filter: blur(0px);
    opacity: 1;
  }
}

@-webkit-keyframes puff-out-bl {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform-origin: 0% 100%;
            transform-origin: 0% 100%;
    -webkit-filter: blur(0px);
            filter: blur(0px);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(2);
            transform: scale(2);
    -webkit-transform-origin: 0% 100%;
            transform-origin: 0% 100%;
    -webkit-filter: blur(4px);
            filter: blur(4px);
    opacity: 0;
  }
}
@keyframes puff-out-bl {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform-origin: 0% 100%;
            transform-origin: 0% 100%;
    -webkit-filter: blur(0px);
            filter: blur(0px);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(2);
            transform: scale(2);
    -webkit-transform-origin: 0% 100%;
            transform-origin: 0% 100%;
    -webkit-filter: blur(4px);
            filter: blur(4px);
    opacity: 0;
  }
}
