.main-section {
	position: relative;
	// background: url(../img/main-bg.jpg) 50% 50% no-repeat;
	// background-size: cover;
	overflow: hidden;

	&:before {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		content: "";
		height: 447px;
		background: linear-gradient(180deg, rgba(31, 33, 38, 0) 0%, #1F2126 100%);
		z-index: 1;
	}

	.container {
		position: relative;
		z-index: 2;
	}

	video {
		position: absolute;
		// height: 100%;
		width: 100%;
		z-index: 0;
		left: 50%;
		transform: translateX(-50%);

		@media (max-width: 1750px) {
			width: auto;
			height: 100%;
		}
	}

	.wrapper {
		position: relative;
		min-height: 980px;		
		display: flex;
		flex-direction: column;
		justify-content: center;

		@media (max-width: $desk) {
			min-height: 780px;
		}

		@media (max-width: $mobile) {
			min-height: 533px;
			padding-bottom: 50px;
		}

		h1,
		.h1 {
			font-size: 58px;
			font-weight: bold;
			color: #fff;
			max-width: 615px;
			line-height: 1.2em;

			@media (max-width: $tablet) {
				font-size: 48px;
			}

			@media (max-width: $mobile) {
				font-size: 30px;
				padding-right: 50px;
				margin: 0 0 20px;
				line-height: 1.1em;
			}

			i {
				font-style: normal;
				font-weight: 300;
			}
		}

		.f_button {
			max-width: 231px;
			padding: 0;

			@media (max-width: $mobile) {
				max-width: 211px;
			}
		}

		.phone-link {
			position: absolute;
			bottom: 40px;
			right: 22px;
			font-size: 14px;
			padding-left: 1.1em;
			display: none;

			@media (max-width: $mobile) {
				display: inline-flex;
				padding-left: 20px;
			}

			&:hover {
				color: #eee5;

				&:before {
					background-color: #eee5;
				}
			}

			&:before {
				position: absolute;
				top: 50%;
				left: 0;
				content: '';
				-webkit-mask: url(../img/phone-icon.svg) no-repeat 50% 50%;
	      mask: url(../img/phone-icon.svg) no-repeat 50% 50%;
	      -webkit-mask-size: 100% auto;
	      mask-size: 100% auto;
	      background-color: #eee;
	      -webkit-transition: all .2s ease-in-out;
	      transition: all .2s ease-in-out;
	      width: 26px;
	      height: 26px;
	      margin-top: -13px;

	      @media (max-width: $mobile) {
	      	width: 16px;
	      	height: 16px;
	      	margin-top: -8px;
	      }
			}
		}

		.rotate-title {
			position: absolute;
			bottom: 124px;
			left: 0;

			@media (max-width: $desk) {
				bottom: 60px;
			}

			@media (max-width: $mobile) {
				bottom: 70px;
			}

			span {
				position: relative;
				display: inline-flex;
				font-size: 16px;
				font-weight: 400;
				opacity: .5;
				padding-left: 50px;
				transform: rotate(-90deg);
				left: -60px;
	      animation: heartbeat 1.5s ease-in-out infinite both;

	      @media (max-width: $mobile) {
	      	font-size: 14px;
	      }

				&:before {
					position: absolute;
					top: 50%;
					left: 0;
					content: '';
					height: 1px;
					background-color: #eee;
					width: 42px;
				}
			}

			
			@-webkit-keyframes heartbeat {
			  from {
	        transform: scale(1) rotate(-90deg);
	        transform-origin: center center;
	        animation-timing-function: ease-out;
			  }
			  10% {
          transform: scale(0.91) rotate(-90deg);
          animation-timing-function: ease-in;
			  }
			  17% {
          transform: scale(0.98) rotate(-90deg);
          animation-timing-function: ease-out;
			  }
			  33% {
          transform: scale(0.87) rotate(-90deg);
          animation-timing-function: ease-in;
			  }
			  45% {
          transform: scale(1) rotate(-90deg);
          animation-timing-function: ease-out;
			  }
			}
			@keyframes heartbeat {
			  from {
			  	transform: scale(1) rotate(-90deg);
			  	transform-origin: center center;
			  	animation-timing-function: ease-out;
			  }
			  10% {
			  	transform: scale(0.91) rotate(-90deg);
			  	animation-timing-function: ease-in;
			  }
			  17% {
			  	transform: scale(0.98) rotate(-90deg);
			  	animation-timing-function: ease-out;
			  }
			  33% {
			  	transform: scale(0.87) rotate(-90deg);
			  	animation-timing-function: ease-in;
			  }
			  45% {
			  	transform: scale(1) rotate(-90deg);
			  	animation-timing-function: ease-out;
			  }
			}
		}
	}
}

.about-us-section {
	position: relative;
	z-index: 1;
	padding-bottom: 160px;

	@media (max-width: $tablet) {
		padding-bottom: 80px;
	}

	@media (max-width: $mobile) {
		padding-bottom: 20px;
	}

	.wrapper {
		position: relative;
		display: flex;
		align-items: center;

		@media (max-width: $mobile) {
			display: block;
			padding-top: 200px;
		}
		

		&:before {
			position: absolute;
			bottom: -170px;
			right: -130px;
			content: "";
			background: url(../img/about-us-flower.svg) 0 0 no-repeat;
			width: 302px;
			height: 319px;
			z-index: -1;

			@media (max-width: $mobile) {
				bottom: -26px;
				right: -50px;
				background-size: 100% auto;
				width: 173px;
				height: 183px;
			}
		}

		.left-block {
			flex: 0 300px;
			width: 300px;
			border-right: 1px solid #fff2;
			min-height: 167px;
			display: flex;
			align-items: center;
			margin-right: 132px;

			@media (max-width: $desk) {
				margin-right: 70px;
				width: 200px;
				flex: 0 200px;
			}

			@media (max-width: $mobile) {
				margin: 0;
				width: 100%;
				min-height: auto;
				margin-bottom: 24px;
				padding-bottom: 24px;
				border: 0;
				border-bottom: 1px solid #fff2;
			}

			.main-title {
				color: #eee;
			}
		}

		.right-block {
			flex: 0 820px;
			width: 820px;
			padding: 40px 60px 40px 0;
			background: rgba(#1F2126, 0.6);

			@media (max-width: $mobile) {
				width: 100%;
				padding: 0;
				background: none;
			}

			.title {
				display: block;
				font-size: 22px;
				line-height: 26px;
				font-weight: bold;
				margin-bottom: 10px;
				padding-right: 280px;

				@media (max-width: $tablet) {
					padding-right: 200px;
				}

				@media (max-width: $mobile) {
					padding: 0;
					font-size: 16px;
					line-height: 1.2em;
				}

				i {
					font-style: normal;
					font-weight: 300;
				}
			}

			.text {
				display: block;
			}

			picture {
				position: absolute;
				top: 50%;
				right: 0;
				z-index: -1;
				transform: translateY(-50%);

				@media (max-width: $tablet) {
					width: 310px;
					top: -50px;
					transform: none;
				}

				@media (max-width: $mobile) {
					width: 100%;
					top: 0;
					left: 50%;
					margin-left: calc(-100% / 2);
				}

				&:before {
					position: absolute;
					top: 0;
			    bottom: 0;
			    left: 0;
			    right: 0;
			    content: "";
			    // height: 447px;
			    background: -webkit-gradient(linear, left top, left bottom, from(rgba(31, 33, 38, 0)), to(#1F2126));
			    background: linear-gradient(180deg, rgba(31, 33, 38, 0) 0%, #1F2126 100%);
			    transition: all .3s;
			    opacity: 0;
			    visibility: hidden;

			    @media (max-width: $mobile) {
			    	opacity: 1;
			    	visibility: visible;
			    }
				}
			}
		}
	}
}

.service-section {
	position: relative;
	padding-bottom: 60px;

	@media (max-width: $mobile) {
		padding-bottom: 0;
	}

	&:before {
		position: absolute;
		left: 0;
		bottom: -10px;
		content: "";
		background: url(../img/service-flower.svg) 0 0 no-repeat;
		width: 343px;
		height: 741px;

		@media (max-width: $mobile) {
			display: none;
		}
	}

	.container {
		@media (max-width: $mobile) {
			padding: 0;
		}
	}

	.main-title {
		display: block;
		text-align: center;
		margin-bottom: 60px;

		@media (max-width: $mobile) {
			margin-bottom: 30px;
		}
	}

	.wrapper {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		margin: -4px;

		.item {
			position: relative;
			margin: 4px;
			transition-delay: .3s;
			min-height: 331px;
			overflow: hidden;

			@media (max-width: $tablet) {
				flex: 0 calc(50% - 8px) !important;
				width: calc(50% - 8px) !important;
				min-height: auto !important;
				height: 400px;
			}

			@media (max-width: $mobile) {
				min-height: 155px !important;
				height: auto;
				display: flex;
				flex-direction: column;
				transition: all .3s;
				flex: 0 calc(100% - 8px) !important;
				width: calc(100% - 8px) !important;
			}

			&:before {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				content: "";
				background: linear-gradient(180deg, rgba(31, 33, 38, 0) 0%, #1F2126 78.12%);
				opacity: 0.7;
				z-index: 1;
			}

			&.w-100 {
				flex: 0 calc(100% - 8px);
				width: calc(100% - 8px);

			}
			&.w-50 {
				flex: 0 calc(50% - 8px);
				width: calc(50% - 8px);

			}
			&.w-33 {
				flex: 0 calc(33.333% - 8px);
				width: calc(33.333% - 8px);

			}
			&.w-25 {
				flex: 0 calc(25% - 8px);
				width: calc(25% - 8px);
			}

			&:first-child {
				min-height: 566px;

				@media (max-width: $desk) {
					min-height: 466px;
				}

				@media (max-width: $mobile) {
					min-height: 320px;
					height: 320px;
					flex: 0 calc(100% - 8px) !important;
					width: 0 calc(100% - 8px) !important;
				}
			}

			&:nth-child(2),
			&:nth-child(3) {
				@media (max-width: $mobile) {
					flex: 0 calc(50% - 8px) !important;
					width: calc(50% - 8px) !important;
					height: auto;
					min-height: 255px !important;
				}
			}

			&:last-child {
				@media (max-width: $mobile) {
					flex: 0 calc(100% - 8px) !important;
					width: calc(100% - 8px) !important;
				}
				.bottom-block {
					ul {
						column-count: 3;
						column-gap: 10px;

						@media (max-width: $tablet) {
							column-count: 1;
						}
					}
				}
			}

			&:hover {
				picture {
					@media (min-width: 768px) {
						transform: scale(1.1);
					}
					@media (max-width: 767px) {
						filter: blur(4px);
					}
				}
				.bottom-block {
					ul {
						opacity: 1;
						visibility: visible;
						height: auto;
					}
				}
			}

			picture {
				display: block;
				width: 100%;
				height: 100%;
				transition: all 1s;

				@media (max-width: $mobile) {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					z-index: -1;
				}

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			.bottom-block {
				position: absolute;
				left: 36px;
				bottom: 36px;
				right: 36px;
				z-index: 2;

				@media (max-width: $desk) {
					left: 10px;
					bottom: 10px;
					right: 10px;
				}

				@media (max-width: $mobile) {
					position: relative;
					left: auto;
					right: auto;
					bottom: auto;
					margin-top: auto;
					padding: 10px;
				}

				.item-name {
					display: block;
					font-size: 26px;
					font-weight: bold;
					margin-bottom: 20px;
					cursor: pointer;

					@media (max-width: $mobile) {
						font-size: 18px;
						margin-bottom: 0;
					}
				}

				ul {
					@include reset-list;
					transition: all .6s;

					@media (max-width: $mobile) {
						height: 0;
						opacity: 0;
						visibility: hidden;
						margin-top: 10px;
					}

					li {
						margin-top: 10px;

						&:first-child {
							margin-top: 0;
						}

						a {
							display: inline;
							border-bottom: 1px solid #eee;
							transition: all .5s;

							&:hover {
								color: #847CFF;
								border-color: #847CFF;
							}
						}
					}
				}
			}
		}
	}
}

.portfolio-section {
	position: relative;
	padding: 60px 0;

	@media (max-width: $mobile) {
		padding-bottom: 50px;
	}

	.container {
		position: relative;

		@media (max-width: $mobile) {
			position: static;
		}
	}

	.swiper-button-next,
	.swiper-button-prev {
		position: absolute;
		left: 22px;
		top: 50%;
		margin: 0;
		background: none;
		width: 87px;
		height: 15px;
		margin-top: -7.5px;

		@media (max-width: $mobile) {
			top: auto;
			bottom: 0;
			left: 50%;
			margin-left: -90px;
		}

		&:after {
			position: absolute;
			top: 0;
			left: 0;
			content: '';
			background: url(../img/slider-arrow-l.svg) 0 0 no-repeat;
			width: 87px;
			height: 15px;
		}

		&:before {
			position: absolute;
			top: 50%;
			right: -20px;
			content: '';
			background: #827AFF;
			width: 20px;
			height: 20px;
			border-radius: 100px;
			transition: all .6s ease-in-out;
			margin-top: -10px;
			opacity: 0;
			z-index: -1;
		}

		&:hover {
			&:before {
				right: calc(50% - 10px);
				opacity: 1;
			}
		}

		&:active,
		&:focus {
			&:before {
				right: 81%;
				opacity: 1;
			}
		}
	}

	.swiper-button-next {
		right: 22px;
		left: auto;
		transform: rotate(180deg);

		@media (max-width: $mobile) {
			right: 50%;
			margin-left: 0;
			left: auto;
			margin-right: -90px;
			bottom: -1px;
		}
	}

	.main-title {
		display: flex;
		justify-content: center;
		margin-bottom: 70px;

		@media (max-width: $mobile) {
			margin-bottom: 38px;
		}
	}

	.slider {

		@media (max-width: $mobile) {
			.swiper-container {
				padding: 0 35px;
			}
		}

		.swiper-slide {
			height: 630px;
			@include delay(animation, 10, 0.25s);

			@media (max-width: $mobile) {
				height: 310px;
			}

			a {
				position: relative;
				display: block;
				height: 100%;

				&[data-video] {
					&:before {
						background: rgba(#1F2126, 0.8) url(../img/play-icon.svg) 50% 50% no-repeat;
					}
				}

				&:hover {
					&:before {
						animation: slide-in-elliptic-right-fwd 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
						opacity: 1;
					}
				}

				&:before {
					position: absolute;
					top: 50%;
					left: 50%;
					content: "";
					background: rgba(#1F2126, 0.8) url(../img/zoom-icon.svg) 50% 50% no-repeat;
					width: 120px;
					height: 120px;
					border-radius: 100px;
					margin-top: -60px;
					margin-left: -60px;
					opacity: 0;
				}
				@-webkit-keyframes slide-in-elliptic-right-fwd {
				  0% {
				    -webkit-transform: translateX(800px) rotateY(-30deg) scale(0);
				            transform: translateX(800px) rotateY(-30deg) scale(0);
				    -webkit-transform-origin: -100% 50%;
				            transform-origin: -100% 50%;
				    opacity: 0;
				  }
				  100% {
				    -webkit-transform: translateX(0) rotateY(0) scale(1);
				            transform: translateX(0) rotateY(0) scale(1);
				    -webkit-transform-origin: -1800px 50%;
				            transform-origin: -1800px 50%;
				    opacity: 1;
				  }
				}
				@keyframes slide-in-elliptic-right-fwd {
				  0% {
				    -webkit-transform: translateX(800px) rotateY(-30deg) scale(0);
				            transform: translateX(800px) rotateY(-30deg) scale(0);
				    -webkit-transform-origin: -100% 50%;
				            transform-origin: -100% 50%;
				    opacity: 0;
				  }
				  100% {
				    -webkit-transform: translateX(0) rotateY(0) scale(1);
				            transform: translateX(0) rotateY(0) scale(1);
				    -webkit-transform-origin: -1800px 50%;
				            transform-origin: -1800px 50%;
				    opacity: 1;
				  }
				}



				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
	}
}

.why-we-section {
	position: relative;
	padding: 70px 0 45px;

	@media (max-width: $tablet) {
		padding-top: 40px;
	}

	@media (max-width: $mobile) {
		padding: 70px 0 50px;
	}

	.top-block {
		padding-bottom: 207px;

		@media (max-width: $tablet) {
			padding-bottom: 100px;
		}

		@media (max-width: $mobile) {
			padding-bottom: 50px;
		}

		.wrapper {
			display: flex;
			align-items: center;

			@media (max-width: $mobile) {
				display: block;
			}

			.left-block {
				position: relative;
				flex: 1 0 540px;
				width: 540px;

				@media (max-width: $desk) {
					flex: 1 0 440px;
					width: 440px;
				}

				@media (max-width: $tablet) {
					flex: 1 0 350px;
					width: 350px;
				}

				@media (max-width: $mobile) {
					width: 100%;
				}

				&:before {
					position: absolute;
			    top: -48px;
			    left: 20px;
			    content: "";
			    background: url(../img/about-us-flower.svg) 0 0 no-repeat;
			    width: 302px;
			    height: 319px;
			    z-index: -1;

			    @media (max-width: $mobile) {
			    	background-size: 100% auto;
			    	width: 220px;
			    	height: 232px;
			    	left: auto;
			    	right: -20px;
			    	top: -5px;
			    }
				}

				.main-title {
					flex-wrap: wrap;
					margin-bottom: 30px;

					@media (max-width: $tablet) {
						margin-left: 5px;
					}

					@media (max-width: $mobile) {
						margin: 0 0 20px;
					}

					i {
						display: block;
						font-weight: 300;
						font-style: normal;
						width: 100%;
					}
				}

				.inner-wrapper {
					display: flex;
					align-items: flex-end;

					@media (max-width: $mobile) {
						flex-wrap: wrap;
					}

					.item {
						flex: 0 1 100px;
						width: 100px;
						margin-left: 65px;
						@include delay(animation, 10, 0.25s);

						@media (max-width: $desk) {
							margin-left: 20px;
						}

						@media (max-width: $mobile) {
							margin: 0;
						}

						&:first-child {
							flex: 0 1 170px;
							width: 170px;
							margin-left: 0;

							@media (max-width: $mobile) {
								width: 100%;
								flex: 0 100%;
								margin-bottom: 26px;
								padding-right: 130px;
							}

