								font-size: 22px;

								@media (max-width: $mobile) {
									font-size: 16px;
								}
							}
						}
						
						.item-count {
							display: block;
							font-size: 32px;
							line-height: 1.1em;
							color: #fff;
							font-weight: 400;
							margin-bottom: 5px;

							@media (max-width: $mobile) {
								font-size: 22px;;
							}
						}

						.item-info {
							display: block;
							font-size: 16px;
							line-height: 1.1em;
							color: #fff;
							font-weight: bold;

							@media (max-width: $mobile) {
								font-size: 14px;
							}
						}
					}
				}
			}

			.right-block {
				position: relative;
				 flex: 0 calc(100% - 540px - 20px);
				 width: calc(100% - 540px - 20px);
				 padding-left: 70px;
				 margin-left: 20px;

				 @media (max-width: $desk) {
				 	flex: 0 calc(100% - 440px - 20px);
				 	width: calc(100% - 440px - 20px);
				 }

				 @media (max-width: $tablet) {
				 	flex: 0 calc(100% - 350px - 20px);
				 	width: calc(100% - 350px - 20px);
				 	margin-left: 30px;
				 	padding-left: 30px;
				 }

				 @media (max-width: $mobile) {
				 	padding: 0;
				 	margin: 0;
				 	width: 100%;
				 	padding-top: 32px;
				 	margin-top: 32px;
				 	border-top: 1px solid rgba(#c4c4c4, 0.3);
				 }

				 &:before {
				 	position: absolute;
				 	top: 50%;
				 	left: 0;
				 	content: '';
				 	border-left: 1px solid rgba(#c4c4c4, 0.3);
				 	height: 242px;
				 	margin-top: calc(-242px / 2);

				 	@media (max-width: $mobile) {
				 		display: none;
				 	}
				 }

				 ul {
				 	@include reset-list;
				 	column-count: 2;
				 	column-gap: 20px;

				 	@media (max-width: $tablet) {
				 		column-count: 1;
				 		column-gap: 0px;
				 	}

				 	li {
				 		margin-bottom: 40px;
				 		font-size: 22px;
				 		line-height: 1.1em;
				 		color: #fff;
				 		font-weight: 300;
				 		@include delay(animation, 10, 0.25s);

				 		@media (max-width: $tablet) {
				 			margin-bottom: 25px;
				 		}

				 		@media (max-width: $mobile) {
				 			margin-bottom: 20px;
				 			font-size: 16px;
				 		}

				 		b {
				 			font-weight: bold;
				 		}
				 	}
				 }
			}
		}
	}

	.bottom-block {
		position: relative;
		background: url(../img/team-big.jpg) 50% 50% no-repeat;
		background-size: cover;

		@media (max-width: $mobile) {
			padding-top: 265px;
			background-size: auto 315px;
			background-position: 50% 0;
		}

		&:before {
			position: absolute;
			bottom: 0;
			top: 0;
			left: 0;
			right: 0;
			content: "";
			background: linear-gradient(180deg, rgba(31, 33, 38, 0) 0%, #1F2126 100%);

			@media (max-width: $mobile) {
				bottom: auto;
				height: 315px;
			}
		}

		.wrapper {
			position: relative;
			display: flex;
			align-items: center;
			min-height: 590px;
			z-index: 1;

			@media (max-width: $desk) {
				flex-direction: column;
				justify-content: center;
				text-align: center;
				min-height: auto;
				padding: 50px 0;
			}

			@media (max-width: $mobile) {
				padding: 0;
				text-align: left;
			}

			.left-block {
				flex: 0 calc(100% - 655px);
				width: calc(100% - 655px);
				padding-right: 250px;

				@media (max-width: $desk) {
					width: 100%;
					flex: 0 100%;
					padding: 0;
					margin-bottom: 30px;
				}

				@media (max-width: $mobile) {
					margin-bottom: 40px;
				}

				.main-title {
					margin-bottom: 15px;

					@media (max-width: $mobile) {
						font-size: 22px;
						margin-bottom: 7px;
					}
				}

				.description {
					display: block;
					font-size: 18px;
					color: rgba(#fff, 0.7);
					font-weight: 500;
					line-height: 1.2em;

					@media (max-width: $mobile) {
						font-size: 14px;
					}
				}
			}

			.right-block {
				flex: 1 0 655px;
				width: 655px;

				@media (max-width: $desk) {
					flex: 0 auto;
					width: auto;
				}

				.inner-wrapper {
					display: flex;
					flex-wrap: wrap;
					margin: -7px;

					@media (max-width: $mobile) {
						display: block;
						margin: 0;
					}

					.f_group {
						flex: 0 calc(50% - 14px);
						width: calc(50% - 14px);
						margin: 7px;
						@include delay(animation, 10, 0.25s);

						@media (max-width: $desk) {
							text-align: left;
						}

						@media (max-width: $mobile) {
							width: 100%;
							margin: 14px 0;
						}

						&:last-child {
							flex: 0 calc(100% - 14px);
							width: calc(100% - 14px);
						}
					}

					.f_button {
						width: 100%;
					}
				}
			}
		}
	}
}

.specialist-section {
	position: relative;
	padding: 45px 0 90px;

	@media (max-width: $tablet) {
		padding: 20px 0 50px;
	}

	@media (max-width: $mobile) {
		padding: 0 0 70px;
	}

	.main-title {
		display: flex;
		justify-content: center;
		margin-bottom: 80px;

		@media (max-width: $mobile) {
			margin-bottom: 32px;
		}

	}
	.wrapper {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		margin: -7px;
		overflow: hidden;

		@media (max-width: $mobile) {
			display: block;
			margin: -36px 0;
		}

		&.visible-less {
			height: 1700px;
			@media (max-width: $mobile) {
				height: 1350px;
			}
			&:before {
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				height: 303px;
				content: '';
				background: linear-gradient(180deg, #1F2126 0%, rgba(31, 33, 38, 0) 100%);
				transform: matrix(1, 0, 0, -1, 0, 0);
				z-index: 2;
			}
		}

		.item {
			flex: 0 calc(25% - 14px);
			width: calc(25% - 14px);
			margin: 7px;
			margin-bottom: 58px;
			transition-delay: .2s;

			@media (max-width: $tablet) {
				flex: 0 calc(33.333% - 14px);
				width: calc(33.333% - 14px);
			}

			@media (max-width: $mobile) {
				width: 100%;
				margin: 36px 0;
			}

			.img-block {
				position: relative;
				height: 440px;
				margin-bottom: 16px;

				@media (max-width: $mobile) {
					height: 360px;
				}

				&:before {
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					content: '';
					background: linear-gradient(180deg, rgba(31, 33, 38, 0) 32%, #1F2126 97%)
				}

				picture {
					display: block;
					height: 100%;
				}

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			.info-block {
				text-align: center;

				.name {
					display: block;
					font-size: 26px;
					font-weight: bold;
					line-height: 1.1em;
					margin-bottom: 10px;

					@media (max-width: $mobile) {
						font-size: 18px;
					}
				}

				.position {
					display: block;
					font-weight: 700;
					color: #827AFF;
					margin-bottom: 15px;

					@media (max-width: $mobile) {
						margin-bottom: 10px;
					}
				}

				.info {
					display: block;
					font-weight: 500;
					color: #fff;
					line-height: 1.1em;
					margin-bottom: 12px;
				}

				.read-more {
					display: inline;
					font-weight: 500;
					color: #fff;
					border-bottom: 1px solid #fff;
					transition: all .5s;

					&:hover {
						color: #827AFF;
						border-color: #827AFF;
					}
				}
			}
		}
	}

	.load-more-block {
		display: flex;
		justify-content: center;
		margin-top: 66px;


		.f_button {
			width: 230px;

			@media (max-width: $mobile) {
				width: 100%;
			}
		}
	}
}

main.content {
	overflow: hidden;
}