// transition delay 
@mixin delay($rule, $number, $value) { 
  @for $i from 1 to ($number + 1) { 
    &:nth-child(#{$i}) { 
      -webkit-#{$rule}-delay: (#{$i*$value}); 
      #{$rule}-delay: (#{$i*$value}); 
    } 
  } 
} 
