.f_button {
	position: relative;
	display: inline-flex;
	align-items: center;
  justify-content: center;
	transition: all .3s;
	background: none;
	border: 0;

	font-size: 17px;
	font-weight: bold;
	line-height: 1em;
	color: #eee;
	padding: 0 50px;
	height: 67px;
	// transition: all 495ms ease-out;
  transition: all 330ms;
  border-radius: 100px;
  background: #7069EE;
  overflow: hidden;
  z-index: 1;

  @media (max-width: $mobile) {
    height: 54px;
    font-size: 14px;
  }

  &:after {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    content: '';
    background-color: #908AFF;
    transform: scale(2) translateX(-80%);
    transition: all 1s ease-in-out;
    z-index: 0;
    border-radius: 100px;
    filter: blur(4px);
  }

  span {
    position: relative;
    z-index: 1;
  }

  &:disabled,
  &[disabled] {
    opacity: .5;
    pointer-events: none;
    cursor: default;

    &:after {
      transform: scale(2) translate(-16%);
    }
  }
	
	&:hover {

    &:after {
      transform: scale(2) translate(-16%);
    }
	}

  &:active {
    &:after {
      background-color: #635CD4;
      transform: scale(2) translate(-16%);
    } 
  }

}

.f_area,
.f_control {
	width: 100%;
	display: block;
	height: 64px;
	background: rgba(#fff, 0.2);
	padding: 0 26px;
	font-size: 16px;
	font-weight: 500;
	color: #fff;
  border: 0;
	border-bottom: 2px solid #C7C7C7;
	transition: all .3s;

	&::-webkit-input-placeholder {
	  opacity: 0.5;
		color: #fff;
	}
	&::-moz-placeholder {
	  opacity: 0.5;
		color: #fff;
	}
	&:-ms-input-placeholder {
	  opacity: 0.5;
		color: #fff;
	}
	&:-moz-placeholder {
	  opacity: 0.5;
		color: #fff;
	}
}

.f_area {
	padding: 20px 25px;
	resize: none;
}

.hamburger {
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  width: 40px;
  // height: 24px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  span {
    width: 40px;
    height: 4px;
    margin-bottom: 6px;
    background: #fff;
    border-radius: 4px;
    -webkit-transition: all .5s cubic-bezier(.08,.81,.87,.71);
    -moz-transition: all .5s cubic-bezier(.08,.81,.87,.71);
    -ms-transition: all .5s cubic-bezier(.08,.81,.87,.71);
    -o-transition: all .5s cubic-bezier(.08,.81,.87,.71);
    transition: all .5s cubic-bezier(.08,.81,.87,.71);

    &.span-1 {
      transform-origin: 3px 0px;
    }

    &.span-3 {
      transform-origin: bottom right;
    }
  }
  
  &.opened {
    span {
      &.span-1 {
        background-color: #fff;
        transform: rotate(45deg) translate(3px);
      }

      &.span-2 {
        background-color: #fff;
        transform: rotate(495deg) translate(5px);
      }

      &.span-3 {
        background-color: #fff;
        transform: rotate(45deg);
        opacity: 0;
      }
    }
  }
}

.main-title {
  display: inline-flex;
  font-size: 36px;
  font-weight: bold;
  line-height: 1.2em;
  color: #fff;

  @media (max-width: $mobile) {
    font-size: 26px;
  }
}

.custom-checkbox {
  display: block;
  cursor: pointer;

  &:first-child {
    margin-top: 0;
  }

  &:hover {
    span {
      color: #fff;
    }
  }

  .f_checkbox {
    position: absolute;
    width: 1px;
    height: 1px;
    appearance: none;
    opacity: 0;
    visibility: hidden;

    &:checked + span {

      &:after {
        transform: scale(1);
      }
    }
  }

  span {
    position: relative;
    display: block;
    font-size: 16px;
    color: rgba(#fff, 0.7);
    padding-left: 30px;
    font-weight: 400;
    transition: all .3s;

    @media (max-width: $mobile) {
      font-size: 12px;
    }

    &:before {
      position: absolute;
      top: 1px;
      left: 0;
      content: "";
      width: 14px;
      height: 14px;
      border: 2px solid #D3D3D3;
      border-radius: 4px;
      transition: all .3s;

    }

    &:after {
      position: absolute;
      top: 5px;
      left: 4px;
      content: "";
      -webkit-mask: url(../img/check-icon.svg) no-repeat 50% 50%;
      mask: url(../img/check-icon.svg) no-repeat 50% 50%;
      -webkit-mask-size: 100% auto;
      mask-size: 100% auto;
      background-color: #D3D3D3;
      -webkit-transition: all .2s ease-in-out;
      transition: all .2s ease-in-out;
      width: 10px;
      height: 10px;
      transform: scale(0);
      // border-radius: 4px;
    }

    a {
      display: inline-flex;
      font-size: 16px;
      color: #fff;
      font-weight: bold;
      text-transform: uppercase;
      text-decoration: underline;

      @media (max-width: $mobile) {
        font-size: 12px;
      }

      &:hover {
        color: #7069EE;
      }
    }
  }
}

.cursor,
.cursor2,
.cursor3{
  position: fixed;
  border-radius: 50%; 
  transform: translateX(-50%) translateY(-50%);
  pointer-events: none;
  left: -100px;
  top: 50%;
  mix-blend-mode: difference;
  -webkit-transition: all 300ms linear;
  transition: all 300ms linear;
}
.cursor{
  background-color: #fff;
  height: 5px;
  width: 5px;
  z-index: 99999;
}
.cursor2,.cursor3{
  height: 36px;
  width: 36px;
  z-index:99998;
  -webkit-transition:all 0.3s ease-out;
  transition:all 0.3s ease-out
}
.cursor2.hover,
.cursor3.hover{
  -webkit-transform:scale(2) translateX(-25%) translateY(-25%);
  transform:scale(2) translateX(-25%) translateY(-25%);
  border:none
}
.cursor2{
  border: 1px solid #fff;
  box-shadow: 0 0 22px rgba(255, 255, 255, 0.7);
}
.cursor2.hover{
  background: #3333;
  box-shadow: 0 0 12px rgba(255, 255, 255, 0.3);
}

@media screen and (max-width: 767px){
  .cursor,.cursor2,.cursor3{
    display: none
  }
}

