.video {
  position: relative;
  width: 100%;
  height: 253px;
  margin-top: 30px;

  @media (max-width: $mobile) {
    height: 200px;
  }

  &:first-child {
    margin-top: 0;
  }

  &:hover {
    .video__button {
      svg {
        fill: #ffcd33;
      }
    }
  }
  .video__link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }

  .video__media {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: none;
  }

  .video__button {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    padding: 0;
    width: 120px;
    height: 120px;
    border: none;
    background-color: transparent;
    transform: translate(-50%, -50%);
    cursor: pointer;
    transition: all .6s;
    background: rgba(#1F2126, 0.8);
    border-radius: 200px;

    @media (max-width: $mobile) {
      width: 92px;
      height: 92px;
    }

    &:hover {
      background: #1F2126;

      svg {
        transform: scale(2);

        @media (max-width: $mobile) {
          transform: scale(1.5);
        }
      }
    }

    svg {
      transition: all .6s;
    }
  }

  /* Enabled */

  &.video--enabled {
    cursor: pointer;
    margin-bottom: 80px;

    @media (max-width: $mobile) {
      margin-bottom: 30px;
    }

    .video__button {
      display: block;
    }
  }
}